<template>
  <div class="mtui-stepper">
    <div
      :class="`mtui-stepper_reduce ${currentValue <= min ? 'disable' : ''} ${ disable ? 'disable' : ''}`"
      @click.stop.prevent="reduce"
    ></div>
    <input
      type="tel"
      :value="currentValue"
      @blur.stop.prevent="handleValue"
      @focus.stop.prevent="focusFn"
      :maxlength="maxLength"
      :class="`mtui-stepper_input ${disableInput ? 'disable' : ''}`"
      :readonly="disableInput"
      ref="inputBox"
      @click.stop.prevent=""
    >
    <div
      :class="`mtui-stepper_add ${currentValue >= max ? 'disable' : ''} ${disable ? 'disable' : ''}`"
      @click.stop.prevent="add"
    ></div>
  </div>
</template>
<script>
import toast from "../toast/";

export default {
  name: "mt-stepper",
  data() {
    return {
      currentValue: this.value
    };
  },
  props: {
    color: String,
    value: {
      type: Number,
      default: 0
    },
    step: {
      type: Number,
      default: 1
    },
    min: {
      type: Number,
      default: 0
    },
    minTip: String,
    max: {
      type: Number,
      default: 999999
    },
    maxTip: String,
    disable: {
      type: Boolean,
      default: false
    },
    disableInput: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    currentValue(val) {
      this.$emit("input", val);
      this.$emit("on-change", val);
    },
    value(val) {
      this.updateValue(val);
    }
  },
  computed: {
    maxLength() {
      return parseInt(this.max.toString().length);
    }
  },
  methods: {
    reduce() {
      if (this.disable) return;
      if (this.min || this.min == 0) {
        if (this.currentValue <= this.min) {
          this.currentValue = this.min;
          if (this.minTip) {
            toast(this.minTip);
          }
        } else if (this.currentValue <= this.step) {
          this.currentValue = this.min;
        } else {
          this.currentValue -= this.step;
        }
        return;
      }
      if (this.currentValue <= this.min) {
        this.currentValue = this.min;
      } else {
        this.currentValue -= this.step;
      }
    },
    add() {
      if (this.disable) return;
      if (this.currentValue >= this.max) {
        this.currentValue = this.max;
        if (this.maxTip) {
          toast(this.maxTip);
        }
      } else {
        this.currentValue += this.step;
      }
    },
    updateValue(val) {
      if (val) {
        if (val <= this.min) val = this.min;
        if (val >= this.max) val = this.max;
        this.currentValue = val;
      } else if (val == 0) {
        this.currentValue = 0;
      } else {
        this.currentValue = this.min;
      }
    },

    handleValue() {
      let val = this.$refs.inputBox.value.trim().replace(/-/g, "");
      const value = parseInt(val);
      const min = this.min;
      const max = this.max;

      if (this.disableInput) {
        this.currentValue = this.value;
        return;
      }
      if (this.isValueNumber(value)) {
        this.currentValue = value;
        this.$refs.inputBox.value = value;
        if (value >= max) {
          this.currentValue = max;
        }
        if (value <= min) {
          this.currentValue = min;
        }
      } else {
        this.$refs.inputBox.value = this.currentValue;
      }
      this.$emit("on-blur", this.currentValue);
    },

    focusFn() {
      this.$emit("on-focus", this.currentValue);
    },

    isValueNumber(val) {
      return /(^-?[0-9]+\.{1}\d+$)|(^-?[1-9][0-9]*$)|(^-?0{1}$)/.test(`${val}`);
    }
  },
  mounted() {
    this.updateValue(this.value);
  }
};
</script>

<style lang='less'>
.mtui-stepper {
  display: inline-block;
  font-size: 0;
  .mtui-stepper_box {
    display: inline-block;
  }
  .mtui-stepper_reduce,
  .mtui-stepper_add,
  .mtui-stepper_input {
    float: left;
    height: 0.48rem;
    line-height: 0.48rem;
    border: 0.01rem solid #e5e5e5;
    text-align: center;
    background-color: white;
    appearance: none;
    outline: none;
    -moz-appearance: textfield; //去除默认上下小按钮
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none !important;
    }
  }

  .mtui-stepper_reduce,
  .mtui-stepper_add {
    width: 0.58rem;
    font-size: 0.4rem;
  }
  .mtui-stepper_reduce {
    margin-right: 0.06rem;
    &.disable {
      color: #e6e6e6;
    }
  }
  .mtui-stepper_add {
    margin-left: 0.06rem;
  }
  .mtui-stepper_reduce:before {
    content: "-";
  }
  .mtui-stepper_add:before {
    content: "+";
  }
  .mtui-stepper_input {
    width: 0.98rem;
    vertical-align: top;
    line-height: 0.48rem;
  }
  .disable {
    background-color: #fafafb;
    border: 0.01rem solid #fafafb;
    color: #e6e6e6;
  }
  &.dark {
    .mtui-stepper_reduce,
    .mtui-stepper_add,
    .mtui-stepper_input {
      background-color: #fafafb;
      border: 0.01rem solid #fafafb;
      color: #e6e6e6;
    }
  }
}
</style>




